* {
    margin: 0;
    padding: 0;
}

@font-face {
    font-family: perfectFont;
    src: url(/src/assets/fonts/perfect_font.ttf);
}

.header {
    background: #ffffff center/cover url("/src/assets/backgrounds/old-notes.png") no-repeat;
    height: 207px;
    border-bottom: 3px solid #d6ccd1;

    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: start;
    padding-left: 10px;
}

.logo {
    margin-top: 10px;
    height: auto;
    margin-bottom: 5px;
}

.verse {
    font-size: 16px;
    font-family: perfectFont;
    font-weight: 700;
    font-style: italic;
    text-align: left;
    line-height: 40px;

    display: flex;
    flex-direction: column;

    color: #777777;
    margin-top: 0;
    margin-left: 15px;
}

